<template>
  <el-form
    :model="form"
    :rules="rules"
    label-position="right"
    label-width="120px"
    style="margin-right: 30px;"
    ref="form">
    <el-form-item :label="$t('profile.account.account') + ':'" prop="mail">
      <span>{{ form.account }}</span>
    </el-form-item>

    <el-form-item :label="$t('profile.account.password') + ':'" prop="pass">
      <el-input
        v-model.trim="form.pass"
        show-password
        placeholder="******"
        clearable
        ref="pass" />
    </el-form-item>

    <el-form-item :label="$t('profile.account.company') + ':'" prop="companyName">
      <el-input
        v-model="form.companyName"
        :placeholder="$t('profile.account.company_name')"
        clearable />
    </el-form-item>
  </el-form>
</template>

<script>

import { mixinValidateForm } from '@/mixins/form-validation';

import validator from '@/utils/validators/rules-validation';
import validPassword from '@/utils/validators/valid-password';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';

export default {
  mixins: [mixinValidateForm],
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      form: {
        id: '', pass: '', account: '', companyName: '',
      },
      rules: {
        pass: [
          // validator.requirement(),
          // ...validator.charLengthRange(6, 20),
          // validator.charTypeNotOnlyDigitOrLatin(),
          validator2ElemRule(validPassword),
        ],
        companyName: [
          validator.spaceTrim(),
          ...validator.charLengthRange(1, 64),
        ],
      },
    };
  },
  watch: {
    data: {
      handler({ id, account, companyName }) {
        Object.assign(this.form, { id, account, companyName });
      },
      immediate: true,
    },
  },
  async mounted() {
    // 把密码类型的input默认设置为明文
    this.$refs.pass.handlePasswordVisible();
    this.$refs.pass.blur();
  },
  methods: {
    resetFormFields() {
      this.$refs.form.resetFields();
    },
  },
};
</script>
