<template>
  <el-form
    class="workspace-pass-wrapper"
    :model="form"
    :rules="rules"
    label-position="right"
    :label-width="!multiple ? '200px' : '130px'"
    ref="form">
    <el-form-item
      v-if="!multiple"
      prop="account"
      :label="$t('profile.account.workspace_pass_id') + ':'"
      :class="{'margin-bottom': mode !== 'create'}">
      <el-input v-model.trim="form.account" />
    </el-form-item>

    <el-form-item v-if="!multiple"
      :label="$t('profile.account.temporary_password') + ':'" prop="pass">
      <el-input
        v-model.trim="form.pass"
        type="password"
        show-password
        :placeholder="$t('profile.account.password')"
        ref="pass" />
    </el-form-item>

    <el-form-item
      :label="$t('profile.account.recipient_email') + ':'" prop="mail" v-if="!multiple">
      <el-input v-model="form.mail"
        :placeholder="$t('profile.account.email')" @focus="showRemarks=true" />
    </el-form-item>
    <el-form-item :label="$t('profile.account.recipient_email') + ':'" v-else>
      <el-input type="textarea" v-model="form.mails" rows="5"
        :placeholder="multipleInvitationPlaceholder" @focus="showRemarks=true"></el-input>
    </el-form-item>

    <el-form-item
      :label="$t('profile.account.remarks') + ':'" v-if="mode !== 'create' && showRemarks">
      <el-input v-model="form.remarks" type="textarea"
        :placeholder="$t('profile.account.remarks_tip')"/>
    </el-form-item>

    <el-form-item v-if="!multiple" prop="companyName" :label="$t('profile.account.company') + ':'">
      <el-input v-model="form.companyName"></el-input>
    </el-form-item>

    <!-- <el-form-item label="" v-if="mode !== 'create'">
      <el-checkbox style="font-size: 12px;"
        v-model="isSendImg"
        class="word-break-normal flex">Include relevant Workspace image when initial Workspace
        Pass e-mail invitation is sent.</el-checkbox>
    </el-form-item> -->

    <el-divider />

    <el-radio-group v-model="radio">
      <el-radio :label="1">
        {{ $t('profile.account.same_as_subs_plan') }}</el-radio>
      <el-radio :label="2">{{ $t('profile.account.cus_acct_exp_date') }}</el-radio>
      <el-date-picker
        v-show="radio === 2"
        v-model="dateRange"
        :default-time="['00:00:00', '23:59:59']"
        :picker-options="PickerOptions"
        type="daterange"
        format="MM/dd/yyyy"
        :start-placeholder="$t('profile.account.start_date')"
        :end-placeholder="$t('profile.account.end_date')"
        @change="changeTime" />
    </el-radio-group>

    <i
      v-if="mode === 'create'"
      class="iconfont icon-circle-help"
      @mouseenter="tipVisible = true"
      @mouseleave="tipVisible = false" />

    <!-- eslint-disable-next-line -->
    <p v-show="tipVisible" class="tip-time">{{ $t('profile.account.email_no_notification_tip') }}</p>
  </el-form>
</template>

<script>
import i18n from 'i18next';
import { randomString } from '_/random-string';
import { startOfDate } from '_/date';
import tokenizeEmails from '@/utils/tokenizeEmails';
import validator, { validateFormFields } from '@/utils/validators/rules-validation';
import validPassword from '@/utils/validators/valid-password';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import { mixinValidateForm } from '../../../../mixins/form-validation';
import { getContextUser } from '../../../../loaders/context';


export default {
  props: {
    multiple: {
      type: Boolean,
      default: false,
    },
    mode: {
      type: String,
      default: 'create',
    },
  },
  mixins: [mixinValidateForm],
  data() {
    this.tokenizeEmails = tokenizeEmails;
    const accountValidator = (rule, value, callback) => {
      const reg = /^[a-zA-Z_0-9]+$/g;
      if (reg.test(value)) {
        const hasNum = /\d/.test(value);
        const hasLetter = /[a-zA-Z]/.test(value);
        const hasUnderLine = /[_]/.test(value);
        const score = [hasNum, hasLetter, hasUnderLine].reduce(((total, v) => total + (v ? 1 : 0)), 0);
        if (score >= 2) {
          callback();
        } else {
          callback(new Error(i18n.t('profile.account.account_validator_tip')));
        }
      } else {
        callback(new Error(i18n.t('profile.account.account_validator_tip')));
      }
    };

    this.multipleInvitationPlaceholder = i18n.t('profile.account.multiple_invitation_placeholder');
    return {
      now: new Date(),
      tipVisible: false,
      dateRange: '',
      PickerOptions: {
        disabledDate: this._disabledRangeDate,
      },
      radio: 1,
      form: {
        account: randomString(6),
        pass: 'Hello123~',
        mail: '',
        mails: '',
        remarks: '',
        startTime: '',
        endTime: '',
        companyName: '',
      },
      isSendImg: false,
      rules: {
        account: [
          validator.requirement(),
          ...validator.charLengthRange(6, 25),
          { validator: accountValidator },
        ],
        pass: [
          validator.requirement(),
          // ...validator.charLengthRange(6, 20),
          // validator.charTypeNotOnlyDigitOrLatin(),
          validator2ElemRule(validPassword),
        ],
        mail: validator.email(),
        companyName: [
          validator.spaceTrim(),
          ...validator.charLengthRange(1, 64),
        ],
      },
      hasFormError: true,
      showRemarks: false,
    };
  },
  mounted() {
    // 把密码类型的input默认设置为明文
    this.$refs.pass.handlePasswordVisible();
    this.$refs.pass.blur();
  },
  created() {
    // 类似于computed：里加一个hasFormError属性
    this.$watch(async () => {
      if (!this.$refs.form || this.mode === 'create') return true;
      try {
        await validateFormFields(this.$refs.form);
      } catch (err) {
        console.warn(err);
        return true;
      }
      const errorLen = this.$refs.form.fields
        .filter(field => field.validateState !== 'success').length;
      // debugger;
      return !!errorLen;
    }, async (_p) => {
      this.hasFormError = await _p;
    }, {
      immediate: true,
    });
  },
  computed: {
    daysRemaining() {
      return getContextUser().daysRemaining;
    },
    multipleEmailsValid() {
      try {
        const emails = tokenizeEmails(this.form.mails);
        if (!emails) return false;
        return true;
      } catch (e) {
        return false;
      }
    },
    dateValid() {
      return this.radio === 1 || (this.radio === 2 && this.form.startTime && this.form.endTime);
    },
    formAndDateValid() {
      return !this.hasFormError && this.dateValid;
    },
    multipleFormAndDateValid() {
      return this.multipleEmailsValid && this.dateValid;
    },
  },
  methods: {
    // 对 datapicker 返回类型处理为时间戳
    changeTime([startTime, endTime]) {
      this.form.startTime = startTime.getTime();
      this.form.endTime = endTime.getTime();
    },
    // 初始化表单的内容
    initForm() {
      this.$refs.form.resetFields();
      this.dateRange = '';
    },
    // 控制日期选中组件中，不可选择日期的范围
    _disabledRangeDate(time) {
      const todaytoday = +startOfDate(new Date());
      const end = todaytoday + (this.daysRemaining + 1) * 24 * 3600 * 1000 - 1;
      return time < todaytoday || time > end;
    },
  },
  watch: {
    multiple(newv) {
      this.showRemarks = false;
      if (newv) {
        this.$closest('InvitationWrapper').okAvailable = this.multipleFormAndDateValid;
      } else {
        this.$closest('InvitationWrapper').okAvailable = this.formAndDateValid;
      }
    },
    multipleFormAndDateValid(newv) {
      this.$closest('InvitationWrapper').okAvailable = newv;
    },
    formAndDateValid(newv) {
      this.$closest('InvitationWrapper').okAvailable = newv;
    },
  },
};
</script>

<style lang="scss" scoped>
.el-form {
  position: relative;
  padding-right: 26px;
  margin-bottom: 30px;

  .el-form-item {
    margin-bottom: 28px;
  }

  > .icon-circle-help {
    position: absolute;
    right: 30px;
    top: 145px;
    z-index: 2;
    padding: 5px;
    font-size: 17px;
    color: $color-primary;
    cursor: pointer;
  }

  .tip-time {
    position: absolute;
    right: 26px;
    top: 175px;
    z-index: 2;
    width: 296px;
    padding: 15px 18px;
    font-size: 12px;
    line-height: 1.4;
    word-break: break-word;
    color: #7f7f7f;
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    box-shadow: 0px 2px 8px 0px rgba(95, 95, 95, 0.3);
  }
}

.el-radio-group {
  display: flex;
  flex-direction: column;
  margin-left: 10px;

  .el-radio {
    margin-top: 20px;
    &:first-child {
      margin-bottom: 10px;
    }
  }
}
.workspace-pass-wrapper {
  /deep/ ::-webkit-input-placeholder {
    white-space: normal;
    word-break: normal;
  }
  /deep/ ::-moz-placeholder {
    white-space: normal;
    word-break: normal;
  }
  /deep/ :-ms-input-placeholder {
    white-space: normal;
    word-break: normal;
  }
}
</style>
