import { i18n } from '@/i18n';
import { checkEmailExistent } from '@/api/email';
import { checkIsMainAccount } from '@/api/account-related';

// eslint-disable-next-line
const reg = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
export function validEmail(email: string) {
  if (!reg.test(email)) {
    throw new Error(i18n.t('validator.invalid_email'));
  }
}


type DynamicVerifyEmailOption = {
  // 检测是否已注册账号 checkAllAccount | checkExist； 检测是否主账号 checkMainAccount
  // checkAllAccount和checkExist都是调checkEmailExistent接口检测邮箱是否已注册
  // 区别是前者强调“已被注册的重复账号”，会报错。后者检测是否为“已注册的合法账号”
  mode: 'checkAllAccount' | 'checkExist' | 'checkMainAccount';
};

// checkExist模式下改写checkEmailExistent的response信息
async function checkExist(email: string, cancelToken = {}) {
  try {
    await checkEmailExistent(email, cancelToken);
    throw new Error(i18n.t('public.form.email_dont_exist'));
  } catch (err) {
    if (err.code !== 131003) {
      throw err;
    }
  }
}

export async function dynamicVerifyEmail(email: string, options: DynamicVerifyEmailOption, cancelToken = {}) {
  let { mode } = options;
  if (!mode) {
    mode = 'checkAllAccount';
  }
  try {
    if (mode === 'checkAllAccount') {
      await checkEmailExistent(email.toLowerCase(), cancelToken);
    } else if (mode === 'checkExist') {
      await checkExist(email.toLowerCase(), cancelToken);
    } else {
      await checkIsMainAccount(email.toLowerCase(), cancelToken);
    }
  } catch (err) {
    if (err.message === 'No permission, please contact the administrator') {
      throw new Error(i18n.t('public.form.account_no_permission'));
    } else if (err.message === 'Account information does not exist') {
      throw new Error(i18n.t('public.form.account_not_exist'));
    }
    throw err;
  }
}

export default validEmail;
