<template>
  <div class="table-internal-container">
    <el-table :data="data" class="custom-table taller-table"
  row-class-name='contact-item'>
      <el-table-column :label="$t('profile.account.workspace_pass_id')"
        show-overflow-tooltip min-width="200">
        <template slot-scope="scope">
          <BaseUser :user="scope.row" />
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.company')"
        show-overflow-tooltip min-width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.companyName || '/' }}</span>
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.recipient_email')"
        show-overflow-tooltip min-width="150">
        <template slot-scope="scope">
          <span>{{ scope.row.recipientEmail || '/' }}</span>
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.workspace')"
        show-overflow-tooltip min-width="150px">
        <template slot-scope="scope">
          <span>{{ scope.row.workspaceName || '/' }}</span>
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.tags')"
        width="400">
        <template slot-scope="scope">
          <tags-auto
            :maxlength="30"
            :maxTagsLen="10"
            :data="scope.row.tags"
            @change="updateTags(scope.row.id, $event);"
            ref="tags" />
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.account_status')" min-width="160">
        <template slot-scope="scope">
          <span v-if="scope.row.overtime > 0">
            {{ $t('profile.account.exp_in', { days: scope.row.overtime }) }}</span>
          <span v-else>
            {{ $t('profile.account.has_exp') }}</span>
        </template>
      </el-table-column>

      <el-table-column label="" width="120px">
        <template slot-scope="scope">
          <div class="operation-box">
            <i
              v-if="$can('system', 'OP0105')"
              class="iconfont iconedit"
              @click="$act(openDialogEditAccount, [scope.row],
                $t('profile.account.edit_success'))" />
            <i
              v-if="$can('system', 'OP0104')"
              class="iconfont icon-file-delete"
              @click="deleteSubaccount(scope.row)" />
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import i18n from 'i18next';
import BaseUser from '@/components/base/User';
import TagsAuto from '@/components/base/TagsAuto.vue';
import {
  updateSubaccountInfo,
  deleteSubaccountById,
  updateTagsOfVirtualAccount,
} from '@/api/account-related';
import DialogEditVirtualAccount from './DialogEditVirtualAccount';

export default {
  components: {
    BaseUser,
    TagsAuto,
  },
  props: {
    data: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentAccount: {},
    };
  },
  methods: {
    async openDialogEditAccount(obj) {
      this.currentAccount = obj;
      const res = await this.$Dialog.dialogify({
        is: DialogEditVirtualAccount,
        props: {
          data: obj,
        },
      }, {
        props: {
          title: i18n.t('profile.account.edit_account'),
          'close-on-click-modal': false,
          width: '520px',
          center: true,
          titleAlign: 'left',
        },
      });
      if (res) {
        try {
          await updateSubaccountInfo([res.id], res);
          this.data.forEach(account => {
            if (account.id === res.id) account.companyName = res.companyName;
          });
        } catch (err) {
          this.$errHandler(err);
        }
      }
    },
    // 根据Id删除对应的账号
    async deleteSubaccount({ id, account }) {
      try {
        const options = {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        };
        const result = await this.$confirmBool(i18n.t('profile.account.delete_account_confirm_message', { account }),
          i18n.t('profile.account.delete_confirm_title'), options);

        if (result) {
          await deleteSubaccountById([id]);
          this.$emit('delete-item', id);
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 修改虚拟账号的标签
    async updateTags(id, tags) {
      try {
        await updateTagsOfVirtualAccount(id, tags);
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.operation-box {
  > .iconfont {
    padding: 5px;
    font-size: 20px;
    margin-right: 10px;
    cursor: pointer;
  }
}
