<template>
  <el-form
    status-icon
    :model="form"
    :rules="rules"
    label-position="right"
    label-width="170px"
    style="margin-right: 30px;"
    @validate="handleFormValidate"
    ref="form">
    <el-form-item :label="$t('profile.account.email') + ':'" prop="mail">
      <!-- <dynamic-validate-email @validate="handleValidateEmail" ref="dynamcValidateEmail" /> -->
      <el-input
        v-model.trim="form.mail"
        maxlength="64" />
    </el-form-item>

    <el-form-item :label="$t('profile.account.temporary_password') + ':'" prop="pass">
      <el-input
        v-model.trim="form.pass"
        show-password
        :placeholder="$t('profile.account.password')"
        auto-complete="new-password"
        clearable
        ref="pass" />
    </el-form-item>

    <el-form-item prop="department" :label="$t('profile.account.department') + ':'">
      <el-input v-model="form.department" clearable />
    </el-form-item>

    <el-form-item :label="$t('profile.account.workspaces') + ':'" class="form-item-title">
      <span>{{ workspaceNumber }}</span>
    </el-form-item>
  </el-form>
</template>

<script>
import i18n from 'i18next';
// import DynamicValidateEmail from '@/components/form/dynamic-validation/DynamicValidateEmail';

import { mixinValidateForm } from '@/mixins/form-validation';

import validator from '@/utils/validators/rules-validation';
import { getWorkspacesNumberOfPackage } from '@/api/resource';
import { dynamicVerifyEmail } from '@/utils/validators/valid-email';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import validPassword from '@/utils/validators/valid-password';

export default {
  // components: {
  //   DynamicValidateEmail,
  // },
  mixins: [mixinValidateForm],
  data() {
    return {
      workspaceNumber: 0,
      isEmailAvailable: false, // 校验邮箱是否重复
      form: {
        mail: '',
        pass: 'Hello123~',
        department: '',
      },
      rules: {
        pass: [
          validator.requirement(),
          // ...validator.charLengthRange(6, 20),
          // validator.charTypeNotOnlyDigitOrLatin(),
          validator2ElemRule(validPassword),
        ],
        mail: [
          { required: true, message: i18n.t('public.form.not_empty'), trigger: 'blur' },
          { type: 'email', message: i18n.t('public.form.invalid_email'), trigger: 'blur' },
          validator2ElemRule(async (email, options) => {
            await dynamicVerifyEmail(email, { mode: 'checkAllAccount', ...options });
          }, { trigger: 'blur' }),
        ],
        department: [...validator.charLengthRange(3, 25)],
      },
    };
  },
  async mounted() {
    // 获取当前登录用户的公司套餐workspace数量
    try {
      this.workspaceNumber = (await getWorkspacesNumberOfPackage()).data;
    } catch (e) {
      this.$errHandler(e);
    }

    // 把密码类型的input默认设置为明文
    this.$refs.pass.handlePasswordVisible();
    this.$refs.pass.blur();
  },
  methods: {
    // 动态判断邮箱是否已注册
    // handleValidateEmail({ value, status }) {
    //   this.form.mail = value;
    //   this.isEmailAvailable = status;
    // },
    handleFormValidate(prop, isValidated, err) {
      if (prop === 'mail') {
        this.isEmailAvailable = isValidated;
      }
    },
    // 校验表单是否通过邮箱校验
    checkOtherValidations() {
      return this.isEmailAvailable;
    },
    // 重置动态校验邮箱状态
    initForm() {
      this.$refs.form.resetFields();
      // this.$refs.dynamcValidateEmail.init();
    },
  },
};
</script>
