<template>
  <div>
    <form-create-subaccount @validate="handleValidateForm" ref="formCreateSubaccount" />

    <div class="flex-justify-flex-end">
      <el-button @click="$emit('cancel')">{{ $t('public.dialog.cancel') }}</el-button>
      <el-button type="primary" @click="$refs.formCreateSubaccount.validateForm()">
        {{ $t('public.dialog.confirm') }}
      </el-button>
      <!-- <el-button @click="$emit('cancel')">Cancel</el-button>
      <el-button type="primary" @click="$refs.formCreateSubaccount.validateForm()">
        Ok
      </el-button> -->
    </div>
  </div>
</template>

<script>

import mixinDialogBasic from '@/mixins/dialog-basic';

import { createPaidSubaccount } from '@/api/account';
import FormCreateSubaccount from './FormCreateSubaccount';

export default {
  components: {
    FormCreateSubaccount,
  },
  mixins: [mixinDialogBasic],
  methods: {
    async handleValidateForm(form) {
      // 校验其他情况是否符合表单要求(校验邮箱是否已注册)
      const checkResult = this.$refs.formCreateSubaccount.checkOtherValidations();
      if (!checkResult) {
        this.$errHandler({ message: this.$t('public.form.email_registered') }, -1);
        return;
      }

      // 发送创建子账号请求；关闭弹框；通知父组件更新列表
      try {
        await createPaidSubaccount(form);
        this.$emit('confirm', true);
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 重置表单内容
    initFormFields() {
      this.$refs.formCreateSubaccount.initForm();
    },
  },
  mounted() {
    this.initFormFields();
  },
  activated() {
    this.initFormFields();
  },
};
</script>
