import axios from './axios';

/**
 * 检查指定资源是否还有空余 (1,子账号; 2,协同空间)
 * @param {String} type 类型
 * @returns {Promise} (data: Boolean)
 */
export const checkResourceAvailableByType = (resourceType) => {
  return axios({
    method: 'get',
    url: '/authcenter/resource/check',
    params: {
      resourceType,
    },
  });
};

/**
 * 获取当前登录用户的公司套餐workspace数量
 * @returns {Promise}
 */
export const getWorkspacesNumberOfPackage = () => {
  return axios({
    method: 'get',
    url: '/authcenter/resource/package/workspace/number',
  });
};

