<template>
  <div>
    <form-edit-virtual-account
      :data="data"
      ref="formEditVirtualAccount"
      @validate="$emit('confirm', $event)" />

    <div class="flex-center">
      <el-button @click="$emit('cancel')">{{ $t('public.dialog.cancel') }}</el-button>
      <el-button type="primary" @click="validateForm">
        {{ $t('public.dialog.confirm') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import mixinDialogBasic from '@/mixins/dialog-basic';
import FormEditVirtualAccount from './FormEditVirtualAccount';


export default {
  components: {
    FormEditVirtualAccount,
  },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  mixins: [mixinDialogBasic],
  mounted() {
    this.resetFormFields();
  },
  activated() {
    this.resetFormFields();
  },
  methods: {
    validateForm() {
      this.$refs.formEditVirtualAccount.validateForm();
    },
    resetFormFields() {
      this.$refs.formEditVirtualAccount.resetFormFields();
    },
  },
};
</script>
