<template>
  <div class="table-internal-container">
    <el-table :data="data" v-loading="loading" class="custom-table taller-table"
  row-class-name='contact-item'>
      <el-table-column :label="$t('profile.account.email')"
        show-overflow-tooltip min-width="180px">
        <template slot-scope="scope">
          <BaseUser :user="scope.row" />
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.user_name')"
        show-overflow-tooltip min-width="120px">
        <template slot-scope="scope">
          <span>{{ scope.row.name || '/' }}</span>
        </template>
      </el-table-column>

      <el-table-column :label="$t('profile.account.department')"
        show-overflow-tooltip min-width="100px">
        <template slot-scope="scope">
          <span>{{ scope.row.department || '/' }}</span>
        </template>
      </el-table-column>

      <el-table-column prop="workspaceNumber"
        :label="$t('profile.account.workspaces_available')" min-width="120px" />

      <el-table-column :label="$t('profile.account.account_type')" min-width="140px">
        <template slot-scope="scope">
          <span>{{ scope.row.roleId | accountRole }}</span>
        </template>
      </el-table-column>

      <el-table-column label="" width="120px">
        <template slot-scope="scope">
          <div class="operation-box">
            <i
              v-if="currentUserId !== scope.row.id && $can('system', 'OP0103')"
              class="iconfont iconedit"
              @click="$act(editAccount, [scope.row], $t('profile.account.edit_success'))" />
            <i
              v-if="currentUserId !== scope.row.id && $can('system', 'OP0102')"
              class="iconfont icon-file-delete"
              @click="deleteSubaccount(scope.row)" />
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import i18n from 'i18next';
import BaseUser from '@/components/base/User';
import { updateSubaccountInfo, deleteSubaccountById } from '@/api/account-related';
import { getContextUser } from '../../../../loaders/context';
import DialogEditSubaccount from './DialogEditSubaccount';

import { mixinTableProps } from './mixins';


export default {
  components: {
    BaseUser,
  },
  inject: ['accountType'],
  mixins: [mixinTableProps],
  data() {
    return {
      currentAccount: {},
    };
  },
  computed: {
    currentUserId() {
      return getContextUser().id;
    },
  },
  methods: {
    async editAccount(obj) {
      this.currentAccount = obj;
      const res = await this.$Dialog.dialogify({
        is: DialogEditSubaccount,
        props: {
          data: obj,
        },
      }, {
        props: {
          title: i18n.t('profile.account.edit_account'),
          'close-on-click-modal': false,
          width: '560px',
          center: true,
          titleAlign: 'left',
        },
      });
      if (res) {
        this.handleEditSubccount(res);
      }
    },
    // 处理编辑子账号信息
    async handleEditSubccount(form) {
      if (form.pass === '') delete form.pass;

      await updateSubaccountInfo([form.id], form);
      this.$emit('update-item', form);
    },
    // 根据Id删除对应的子账号
    async deleteSubaccount({ id, mail }) {
      try {
        const options = {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        };
        const result = await this.$confirmBool(i18n.t('profile.account.delete_confirm_message', { mail }),
          i18n.t('profile.account.delete_confirm_title'), options);

        if (result) {
          await deleteSubaccountById([id]);
          this.$message.success(i18n.t('profile.account.delete_success'));
          this.$emit('delete-item', id);
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.operation-box {
  > .iconfont {
    padding: 5px;
    font-size: 20px;
    margin-right: 10px;
    cursor: pointer;
  }
}
</style>
