<template>
  <div>

    <form-invite-account ref="formInviteAccount" @validate="handleValidateForm" />

    <div class="flex-center flex-column">
      <el-button
        type="primary"
        @click="$refs.formInviteAccount.validateForm()">
        {{ $t('profile.account.send_invitation') }}
      </el-button>
      <p style="margin: 15px 0;">
        {{ $t('profile.account.no_email') }}
        <span class="text-link" @click="$act(handleCopylinkDialogOpen, [])">
          {{ $t('profile.account.copy_the_link') }}
        </span>
        {{ $t('profile.account.and_send') }}
      </p>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'element-ui';
import i18n from 'i18next';
import mixinDialogBasic from '@/mixins/dialog-basic';
import { sendEmailByType } from '@/api/email';
import FormInviteAccount from './FormInviteAccount';


import DialogCopyLink from './DialogCopyLink';


export default {
  components: {
    FormInviteAccount,
    // eslint-disable-next-line
    DialogCopyLink,
  },
  mixins: [mixinDialogBasic],
  methods: {
    async handleCopylinkDialogOpen() {
      await this.$Dialog.dialogify({
        is: DialogCopyLink,
      }, {
        props: {
          title: i18n.t('profile.account.link_invitation'),
          width: '580px',
          'close-on-click-modal': false,
          center: true,
        },
      });
    },
    async handleValidateForm({ email, remark }) {
      // 邮件类型：1：关联外部人员到公司邀请邮件，2：关联外部人员到workspace，3”：新增人员发送通知邮件
      const data = {
        email,
        params: {
          email,
          remark,
          groupAppId: '',
          roleId: '',
        },
        type: 1,
      };
      try {
        await sendEmailByType(data);
        this.$refs.formInviteAccount.resetFormFields();
        this.$message.success(i18n.t('public.send_success'));
        this.$emit('confirm', true);
      } catch (err) {
        if (err.message === 'params invalidate: invited user already in your company') {
          MessageBox.alert(i18n.t('workspace.exist_in_company'), i18n.t('public.dialog.notice'));
        } else {
          this.$errHandler(err);
        }
      }
    },
  },
};
</script>
