<template>
  <el-table :data="data" v-loading="loading" class="custom-table taller-table"
  row-class-name='contact-item'>
    <el-table-column :label="$t('profile.account.email')"
      show-overflow-tooltip min-width="200px">
      <template slot-scope="scope">
        <BaseUser :user="scope.row" />
      </template>
    </el-table-column>

    <el-table-column :label="$t('profile.account.user_name')"
      show-overflow-tooltip min-width="150px">
      <template slot-scope="scope">
        <span>{{ scope.row.userName || '/' }}</span>
      </template>
    </el-table-column>

    <el-table-column :label="$t('profile.account.company_name')"
      show-overflow-tooltip min-width="150px">
      <template slot-scope="scope">
        <span>{{ scope.row.companyName || '/' }}</span>
      </template>
    </el-table-column>

    <el-table-column :label="$t('profile.account.status')" min-width="150px">
      <template slot-scope="scope">
        <span v-if="scope.row.status=='3'" class="color-expire">
          {{ $t('profile.account.expired') }}
        </span>
        <span v-if="scope.row.status=='2'">{{ $t('profile.account.normal') }}</span>
      </template>
    </el-table-column>

    <el-table-column v-if="$can('system', 'OP0101')" width="160px">
      <template slot-scope="scope">
        <div class="handle-box" @click="disconnectAccount(scope.row)">
          <i class="iconfont iconduankailianjie" />
          <span class="disconnect-text">{{ $t('profile.account.disconnect') }}</span>
        </div>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import i18n from 'i18next';
import BaseUser from '@/components/base/User';
import { disconnectAccountById } from '@/api/account-related';
import { mixinTableProps } from './mixins';


export default {
  components: {
    BaseUser,
  },
  mixins: [mixinTableProps],
  methods: {
    async disconnectAccount({ id, mail }) {
      try {
        const options = {
          confirmButtonText: i18n.t('public.dialog.real_confirm'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        };
        const result = await this.$confirmBool(
          i18n.t('profile.account.disconnect_confirm_message', { mail }),
          i18n.t('profile.account.disconnect_confirm_title'), options,
        );

        if (result) {
          await disconnectAccountById(id);
          this.$message.success(i18n.t('profile.account.disconnect_success'));
          this.$emit('delete-item', id);
        }
      } catch (err) {
        this.$errHandler(err);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.handle-box {
  @extend %align-vertical-center;
  color: #848b97;
  cursor: pointer;
  &:hover {
    color: $color-primary;
  }

  > .iconfont {
    margin-right: 3px;
    font-size: 20px;
    color: inherit;
  }
  .disconnect-text {
    font-size: 13px;
    color: inherit;
  }
}
</style>
