<template>
  <div>
    <div class="text-align-left">
      <p class="email-tooltip">{{ $t('profile.account.email_tooltip') }}</p>

      <div class="flex">
        <el-input :value="copyContent" :row="2" resize="none" disabled />
        <el-button
          class="btn-copy"
          type="primary"
          :data-clipboard-text="copyContent">{{ $t('profile.account.copy_link') }}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard';
import i18n from 'i18next';

import mixinDialogBasic from '@/mixins/dialog-basic';

import { generateInvitationLink } from '@/api/email';

export default {
  mixins: [mixinDialogBasic],
  data() {
    return {
      copyContent: '',
      clipboard: null,
    };
  },
  async mounted() {
    this.clipboard = this.clipboard || new Clipboard('.btn-copy');
    this.clipboard.on('success', () => {
      this.$message.success(i18n.t('profile.account.copy_success'));
    });
    this.clipboard.on('error', () => {
      this.$message.warning(i18n.t('profile.account.copy_fail'));
    });
    try {
      // 邮件类型：1：关联外部人员到公司邀请邮件，2：关联外部人员到workspace，
      this.copyContent = (await generateInvitationLink({
        type: 1,
        groupAppId: '',
        roleId: '',
      })).data;
    } catch (err) {
      this.$errHandler(err);
    }
  },
  destroyed() {
    this.clipboard.destroy();
  },
};
</script>

<style lang="scss" scoped>
.text-align-left {
  padding-bottom: 60px;
}
.email-tooltip {
  margin-bottom: 15px;
}
.btn-copy {
  margin-left: 5px;
  width: 120px;
}
</style>
