<template>
  <div class="profile-account-container">
    <nav class="account-nav flex-space-between">
      <!-- 切换内外部账号按钮 -->
      <el-radio-group :value="currentComp" @input="handleChangeTableList">
        <el-radio-button
          v-for="item in radioGroup"
          :key="item.name"
          :label="item.label">
          {{ item.name }}
        </el-radio-button>
      </el-radio-group>

      <!-- 搜索输入框 -->
      <div class="account-search-box flex-center">
        <el-input
          v-model="searchKeyword_"
          :placeholder="$t('profile.account.search_placeholder')"
          prefix-icon="el-icon-search"
          clearable
          @input="search" />

        <!-- 新增账号 -->
        <template v-for="item in iconGroup">
          <el-tooltip
            v-if="item.label === currentComp"
            :key="item.tip"
            :content="item.tip">
            <i
              :class="item.icon"
              class="iconfont"
              style="cursor: pointer"
              @click="$act(openDialog, [item])" />
          </el-tooltip>
        </template>
      </div>
    </nav>

    <div class="table-component-box">
      <component
        :is="currentComp"
        :data="table"
        :loading="loadingStatus"
        @update-item="handleUpdateTableItem"
        @delete-item="handleDeleteTableItem" />
    </div>
  </div>
</template>

<script>

import { debounce } from 'throttle-debounce';
import i18n from 'i18next';

import { checkResourceAvailableByType } from '@/api/resource';
import axios from '@/utils/axios';
import {
  callbackWhenLastElementShownNotCompositionVersion,
} from '../../../utils/composables/use-callback-when-last-element-shown';
import { getContextUser } from '../../../loaders/context';
import TableInternalContacts from './components/TableInternalContacts';
import TableExternalContacts from './components/TableExternalContacts';
import TableShareableLink from './components/TableShareableLink';

import DialogCreateSubaccount from './components/DialogCreateSubaccount';
import DialogInviteAccount from './components/DialogInviteAccount';
import DialogCopyLink from './components/DialogCopyLink';
import DialogShareableLink from './components/DialogShareableLink';


export default {
  components: {
    TableInternalContacts,
    TableExternalContacts,
    TableShareableLink,
    DialogCreateSubaccount,
    DialogInviteAccount,
    DialogCopyLink,
    DialogShareableLink,
  },
  props: {
    searchKeyword: String,
    currentComp: {
      type: String,
      default: 'TableInternalContacts',
    },
  },
  provide() {
    return {
      accountType: this.accountType,
    };
  },
  data() {
    return {
      table: [],
      loadingStatus: true, // 表格的loading状态
      searchKeyword_: this.searchKeyword,
      groupList: [
        'TableInternalContacts', 'TableExternalContacts', 'TableShareableLink',
      ],
      iconGroup: [
        {
          // 不是主账号时，不显示创建账号的按钮
          label: this.$can('system', 'OP0001') ? 'TableInternalContacts' : 'Others',
          dialogName: 'DialogCreateSubaccount',
          tip: i18n.t('profile.account.new_account'),
          icon: 'icon-invitation-o',
          title: i18n.t('profile.account.paid_member_account'),
          component: DialogCreateSubaccount,
        },
        {
          label: this.$can('system', 'OP0003') ? 'TableExternalContacts' : 'Others',
          dialogName: 'DialogInviteAccount',
          tip: i18n.t('profile.account.invite_external_account'),
          icon: 'iconyaoqing1',
          title: i18n.t('profile.account.invite_external_contacts'),
          component: DialogInviteAccount,
        },
        {
          label: this.$can('system', 'OP0002') ? 'TableShareableLink' : 'Others',
          dialogName: 'DialogShareableLink',
          tip: i18n.t('profile.account.workspace_pass'),
          icon: 'iconxinjianxuniID',
          title: i18n.t('profile.account.create_workspace_pass'),
          component: DialogShareableLink,
        },
      ],

      pageNumber: 1,
      pageSize: 10,
      pages: 0,
    };
  },
  computed: {
    user() {
      return getContextUser();
    },
    radioGroup() {
      return [
        { name: i18n.t('profile.account.internal_contacts'), label: 'TableInternalContacts' },
        { name: i18n.t('profile.account.external_contacts'), label: 'TableExternalContacts' },
        ...(this.user.businessType === 1 ? [] : [{ name: i18n.t('profile.account.workspace_pass'), label: 'TableShareableLink' }]),
      ];
    },
    accountType() {
      return this.user.roleId;
    },
  },
  async mounted() {
    this.$watch('searchKeyword', (v) => {
      this.searchKeyword_ = v;
    });

    this.$watch(() => {
      return {
        currentComp: this.currentComp,
        searchKeyword: this.searchKeyword,
      };
    }, async () => {
      this.loadingStatus = true;
      this.table = await this._fetchTableDataByPage(
        this.currentComp,
        this.searchKeyword,
        1,
      );
      this.pageNumber = 1;
      this.loadingStatus = false;
    }, {
      immediate: true,
    });

    callbackWhenLastElementShownNotCompositionVersion.call(this, 'table', async () => {
      if (this.pages < this.pageNumber + 1) return;
      this.loadingStatus = true;
      this.table.push(...await this._fetchTableDataByPage(
        this.currentComp,
        this.searchKeyword,
        ++this.pageNumber,
      ));
      // console.log(this.table.map(e => e.account));
      this.loadingStatus = false;
    }, '.contact-item');
  },
  methods: {
    // 根据切换到不同的标签，获取不同表格的数据
    handleChangeTableList(currentComp) {
      this.$router.replace({
        query: {
          currentComp,
          searchKeyword: '',
        },
      });
    },
    // 搜索关键字
    // eslint-disable-next-line
    search: debounce(300, function (v) {
      this.$router.replace({ query: { currentComp: this.currentComp, searchKeyword: v } });
    }),
    // 打开对应的弹框
    async openDialog(ref) {
      const refName = ref.dialogName;
      // 如果是创建子账号弹框，先检查是否有剩余账号；其他类型直接打开弹框
      if (refName === 'DialogCreateSubaccount') {
        const result = (await checkResourceAvailableByType(1)).data;
        if (!result) throw new Error(i18n.t('profile.account.used_up'));
      }
      const res = await this.$Dialog.dialogify({
        is: ref.component,
      }, {
        props: {
          width: '580px',
          title: ref.title,
          'close-on-click-modal': false,
          center: true,
        },
      });
      if (res) {
        this.loadingStatus = true;
        this.pageNumber = 1;
        this.table = await this._fetchTableDataByPage(
          this.currentComp,
          this.searchKeyword,
        );
        this.loadingStatus = false;
      }
    },
    // 根据id, 删除table里面的item
    handleDeleteTableItem(id) {
      const idx = this.table.findIndex((item) => item.id === id);
      if (idx !== -1) this.table.splice(idx, 1);
    },
    // 根据id，修改对应的属性
    handleUpdateTableItem(obj) {
      const idx = this.table.findIndex((item) => item.id === obj.id);
      if (idx !== -1) {
        Object.keys(obj).forEach(prop => {
          this.table[idx][prop] = obj[prop];
        });
      }
    },
    // 分页版获取表格数据
    async _fetchTableDataByPage(
      currentComp, keyword = '', pageNumber = 1, pageSize = this.pageSize,
    ) {
      let url;
      try {
        if (currentComp === this.groupList[0]) {
          url = '/authcenter/user/list/page';
        } else if (currentComp === this.groupList[1]) {
          url = '/authcenter/contacts/list/page';
        } else if (currentComp === this.groupList[2]) {
          url = '/authcenter/virtual/list';
        }
        const res = (await axios({
          url,
          method: 'get',
          params: {
            keyword,
            pageNumber,
            pageSize,
          },
        })).data;
        if (res.pages) {
          this.pages = res.pages;
        }
        return res?.records || [];
      } catch (e) {
        return [];
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.profile-account-container {
  min-height: calc(100vh - 60px - 52px);
  padding: 30px 40px;
  background-color: #fff;

  .account-search-box {
    > .el-input {
      width: 300px;
      margin-right: 5px;
    }

    .iconfont {
      @extend %flex-center;
      width: 40px;
      height: 40px;
      margin-left: 15px;
      font-size: 22px;
      border: 1px solid #dcdfe6;
      border-radius: 4px;
      color: #2878ff;
    }
  }

  .table-component-box {
    margin-top: 30px;
  }
}
</style>
