<template>
  <div>

    <form-shareable-link ref="formShareableLink" @validate="handleValidateForm" />

    <div class="flex-justify-flex-end">
      <el-button @click="$emit('cancel')">{{ $t('public.dialog.cancel') }}</el-button>
      <el-button type="primary" @click="$refs.formShareableLink.validateForm()">
        {{ $t('public.dialog.confirm') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import i18n from 'i18next';

import mixinDialogBasic from '@/mixins/dialog-basic';

import { createVirtualAccount } from '@/api/account-related';
import FormShareableLink from './FormShareableLink';

export default {
  components: {
    FormShareableLink,
  },
  mixins: [mixinDialogBasic],
  methods: {
    async handleValidateForm(form) {
      try {
        await createVirtualAccount(form);
        this.$message.success(i18n.t('profile.account.add_success'));
        this.$emit('confirm', true);
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 重置表单内容
    initFormFields() {
      this.$refs.formShareableLink.initForm();
    },
  },
};
</script>
