import originAxios from 'axios';
import axios, { axiosEncrypt } from '@/utils/axios';

/* ======================= 账号相关 ============================ */
/**
 * 校验账号是否为主账号或普通账号
 * @param {String} id 账号的id
 * @returns {Promise}
 */
export const checkIsMainAccount = (account, cancelToken = {}) => {
  const { CancelToken } = originAxios;
  const source = CancelToken.source();
  const req = axios({
    method: 'get',
    url: '/authcenter/user/check/role',
    params: {
      account,
    },
    cancelToken: source.token,
  });
  cancelToken.cancel = source.cancel;
  return req;
};

/* ======================= 子账号相关 ============================ */
/**
 * 删除账号
 * @param {Array} ids 帐号id的数组
 * @returns {Promise}
 */
export const deleteSubaccountById = (ids) => {
  return axios({
    method: 'get',
    url: '/authcenter/user/account/delete',
    headers: {
      targetIds: JSON.stringify(ids),
    },
  });
};

/**
 * 修改账号信息
 * @param {Array} ids 帐号id的数组
 * @param {Object} data 编辑信息对象
 * @returns {Promise}
 */
export const updateSubaccountInfo = (ids, data) => {
  return axiosEncrypt({
    method: 'post',
    url: '/authcenter/user/account/echo/update',
    headers: {
      targetIds: JSON.stringify(ids),
    },
    data,
  });
};

/* ====================== 关联（邀请）账号相关 ====================== */
/**
 * 解除关联关系（邀请的账号）
 * @param {String} id 删除id
 * @returns {Promise}
 */
export const disconnectAccountById = (id) => {
  return axios({
    method: 'get',
    url: '/authcenter/contacts/disconnect',
    params: {
      relateUserId: id,
    },
  });
};


/* ====================== 虚拟账号相关 ====================== */
/**
 * 新增虚拟账号
 * @param {Object} data 账号信息对象
 * @returns {Promise}
 */
export const createVirtualAccount = (data) => {
  return axiosEncrypt({
    method: 'post',
    url: '/authcenter/user/virtual/add',
    data,
  });
};

/**
 * 更新虚拟账号标签
 * @param {String} userId 账号ID
 * @param {Array} tags 账号标签
 * @returns {Promise}
 */
export const updateTagsOfVirtualAccount = (userId, tags) => {
  return axios({
    method: 'post',
    url: '/authcenter/user/virtual/tag/add',
    data: {
      userId,
      tags,
    },
  });
};
